<template>
  <div id="service-order-form">
    <el-form :inline="true" :model="services">
      <el-form-item label="服务类型">
        <el-select v-model="services.type" placeholder="服务类型">
          <el-option :label="item.title" :value="item.id" v-for="item in serviceType" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="支付状态">
        <el-select v-model="services.payStatus" placeholder="支付状态">
          <el-option :label="item.name" :value="item.category" v-for="item in payStatus" :key="item.category"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="服务状态">
        <el-select v-model="services.serviceStatus" placeholder="服务状态">
          <el-option :label="item.name" :value="item.category" v-for="item in serviceStatus" :key="item.category"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="雇主姓名">
        <el-input v-model="services.employer" placeholder="雇主姓名"></el-input>
      </el-form-item>
      <!-- <el-form-item label="服务时间">
        <el-date-picker
          v-model="services.serviceTime"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions">
        </el-date-picker>
      </el-form-item> -->
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
        <el-button type="default" @click="clear">清除筛选项</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 服务订单列表查询条件表单
 */
import { shortcuts, makeStatusMap } from './orderForm.config'
import { getSerOrdersConditions } from '../../../service/manageOrdersService'
export default {
  name: 'serviceOrderFrom',
  data () {
    return {
      services: {
        type: -1,
        payStatus: 'all',
        serviceStatus: 'all',
        employer: '',
        serviceTime: ''
      },
      serviceType: [{ id: -1, title: '全部' }],
      payStatus: [{ name: '全部', category: 'all' }],
      serviceStatus: [{ name: '全部', category: 'all' }],
      pickerOptions: {
        shortcuts
      },
      conditions: {}
    }
  },
  created () {
    this.fetchConditions()
  },
  methods: {
    fetchConditions () {
      getSerOrdersConditions().then(res => {
        this.conditions = res.data
        this.$store.dispatch('commitChangeOrderStatus', makeStatusMap(this.conditions))
        this.fetchServiceType()
        this.fetchPayStatus()
        this.fetchServiceStatus()
        this.$emit('fetchServiceOrders')
      })
    },
    fetchServiceType () {
      this.serviceType = this.serviceType.concat(this.conditions.service_type)
    },
    fetchPayStatus () {
      this.payStatus = this.payStatus.concat(this.conditions.pay_status)
    },
    fetchServiceStatus () {
      this.serviceStatus = this.serviceStatus.concat(this.conditions.service_status)
    },
    search () {
      this.$emit('searchOrders', this.services)
    },
    clear () {
      this.services = {
        type: -1,
        payStatus: 'all',
        serviceStatus: 'all',
        employer: '',
        serviceTime: ''
      }
      this.$emit('clearSearchConditions', this.services)
    }
  }
}
</script>
<style scoped>

</style>



