<template>
  <div id="service-orders" class="service-orders-container">
    <el-row>
      <el-col>
        <el-card class="card-box pt-0" shadow="hover">
          <service-search-form
            @searchOrders="searchOrders" @clearSearchConditions="clearSearchConditions"
            @fetchServiceOrders="fetchOrders"></service-search-form>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-card class="card-box" shadow="hover">
          <el-row>
            <el-col><span class="count">数据统计：共{{total}}条</span></el-col>
          </el-row><br>
          <el-row>
            <el-col>
              <service-order-table :tableData="orders" :isPagination="isPagination"
                :page="currentPage" :total="total" :fields="fields"
                @changeCurrentPage="changeCurrentPage" @checkSerOrderDetail="checkSerOrderDetail"></service-order-table>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 服务订单列表
 *  */
import ServiceSearchForm from './ServiceOrderForm'
import ServiceOrderTable from './OrderTable'
import { serviceOrdersFields, transServiceOrders } from './orderTable.config'
import { transParamsToStr } from './orderForm.config'
import { getServiceOrders } from '../../../service/manageOrdersService'
export default {
  name: 'serviceOrders',
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      searchParams: {},
      isPagination: true,
      fields: serviceOrdersFields,
      orders: []
    }
  },
  components: {
    'service-search-form': ServiceSearchForm,
    'service-order-table': ServiceOrderTable
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['订单管理', '服务订单'])
  },
  methods: {
    searchOrders (params) {
      this.searchParams = params
      this.fetchOrders()
    },
    clearSearchConditions (params) {
      this.searchParams = params
      this.fetchOrders()
    },
    fetchOrders () {
      this.searchParams['page'] = this.currentPage
      this.searchParams['perPage'] = this.pageSize
      getServiceOrders(transParamsToStr(this.searchParams)).then(res => {
        let orderStatus = this.$store.getters.getOrderStatus
        console.log(orderStatus)
        this.orders = transServiceOrders(res.data.items, orderStatus)
        this.total = res.data.total
      })
    },
    changeCurrentPage (currentPage) {
      this.currentPage = currentPage
      this.fetchOrders()
    },
    checkSerOrderDetail (order) { // 查看服务订单详情
      this.$router.push(`/serviceOrderDetail/${order.orderId}`)
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
</style>



